import React, { useState } from 'react';
import './S_Header.scss'; 

import { HiBars3 } from "react-icons/hi2";

function Header({callbackMenu}) { 
  const [is_menu, setIsMenu] = useState(false)
  
  return (
    <section data-scroll-section>
      <ul className='menu__wrapper'>
        <div className='menu__wrapper_block'>
          <li onClick={() => callbackMenu(1)}>  </li>
          <li onClick={() => callbackMenu(2)}></li>
          <li onClick={() => callbackMenu(3)}>  </li>          
        </div>
        <div className='menu__wrapper_block'>
          <li onClick={() => callbackMenu(4)}> </li>
          <li onClick={() => callbackMenu(5)}>FAQ</li>
          <li onClick={() => callbackMenu(6)}></li>          
        </div>
        <HiBars3 className='mobile_menu' onClick={() => setIsMenu(!is_menu)}/>
      </ul>

      {is_menu ?
        <div className='MobileMenu'>
          <li onClick={() => {callbackMenu(1); setIsMenu(!is_menu)}}>  </li>
          <li onClick={() => {callbackMenu(2); setIsMenu(!is_menu)}}></li>
          <li onClick={() => {callbackMenu(3); setIsMenu(!is_menu)}}>  </li>   
          <li onClick={() => {callbackMenu(4); setIsMenu(!is_menu)}}> </li>
          <li onClick={() => {callbackMenu(5); setIsMenu(!is_menu)}}>FAQ</li>
          <li onClick={() => {callbackMenu(6); setIsMenu(!is_menu)}}></li>      
        </div> : null   
      }

      {is_menu ?
        <div className='blacSCreenMenu' onClick={() => setIsMenu(false)}></div> : null   
      } 

      <div className='logo'></div>
    </section>
  );
}

export default Header;